/* Slider */

.slick-slider {
  position: relative;
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;

  &:focus {
    outline: none;
  }

  &.dragging {
    cursor: pointer;
    cursor: hand;
  }
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;

  &:before,
  &:after {
    content: "";
    display: table;
  }

  &:after {
    clear: both;
  }

  .slick-loading & {
    visibility: hidden;
  }
}

.slick-slide {
  float: left !important;
  height: 100%;
  margin-top: 0 !important;
  min-height: 1px;
  outline: none;
  [dir="rtl"] & {
    float: right;
  }
  &:focus {
    outline: none;
  }
  img {
    display: block;
  }
  &.slick-loading img {
    display: none;
  }

  display: none;

  &.dragging img {
    pointer-events: none;
  }

  .slick-initialized & {
    display: block;
  }

  .slick-loading & {
    visibility: hidden;
  }

  .slick-vertical & {
    display: block;
    height: auto;
    border: 1px solid transparent;
  }
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.top_articles .slick-dots {
  top: 350px;
  right: 0;
  text-align: right;
}

.slick-dots {
  position: absolute;
  left: 50%;
  bottom: 1rem;
  list-style: none;
  display: block;
  text-align: left;
  padding: 0;
  margin: 0;
  width: 20%;
}

.slick-dots {
  li {
    position: relative;
    display: inline-block;
    height: .6rem;
    width: .6rem;
    margin: 0;
    padding: 0;
    cursor: pointer;
  }
  li + li {
    margin-left: .9rem;
  }

}

.slick-dots li button, .slick-dots li button:focus {
  border: 0;
  background: #cae9e5;
  display: block;
  height: .6rem;
  width: .6rem;
  outline: none;
  line-height: 0;
  font-size: 0;
  color: transparent;
  padding: 0;
  cursor: pointer;
}

.slick-dots li.slick-active button,
.slick-dots li button:hover {
  background: #a74c9d;
  outline: none;
}