@import 'partials/normalize.scss';
@import 'partials/mixins.scss';
@import 'partials/fonts.scss';
@import 'partials/slick.scss';

$sans: 'Proba Pro', sans-serif;
$serif: 'Palatino Linotype', serif;
$p-color: #513392;
$s-color: #a9509f;
$t-color: #b86fb0;
$sec-color: #6dc7bd;
$accent: #a74c9d;
$top-nav: #fff;
$body-color: #fafafa;
$top-color: #ededed;
/* ==========================================================================
   Base styles
   ========================================================================== */

html {
  box-sizing: border-box;
  color: #000;
  font-size: 20px;
  line-height: 1.4;
}

*, *:before, *:after {
  box-sizing: inherit;
}

h1,h2,h3,h4,h5,h6 {
  font-weight: normal;
}

/*
 * Remove text-shadow in selection highlight:
 * https://twitter.com/miketaylr/status/12228805301
 *
 * These selection rule sets have to be separate.
 * Customize the background color to match your design.
 */

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

/*
 * A better looking default horizontal rule
 */

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #e8d4e6;
  margin: 1.38em 0 1.2em;
  padding: 0;
}

/*
 * Remove the gap between audio, canvas, iframes,
 * images, videos and the bottom of their containers:
 * https://github.com/h5bp/html5-boilerplate/issues/440
 */

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

/*
 * Remove default fieldset styles.
 */

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

/*
 * Allow only vertical resizing of textareas.
 */

textarea {
  resize: vertical;
}

/* ==========================================================================
   Author's custom styles
   ========================================================================== */

body {
  background: $body-color;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

%center {
  margin: 0 auto;
  max-width: 1000px;
}

%center-padding {
  margin: 0 auto;
  max-width: 1000px;
  padding: 0 1rem;
}

.main-content {
  @extend %center-padding;
}

.page-header {
  margin: 3.8% 0 2.8%;
}

.top-banner {
  background: $sec-color;
  height: 40px;
  &__inner {
    @extend %center-padding;
    position: relative;
    height: 40px;
  }
}

.basic-list {
  position: absolute;
  top: 0;
  left: 2%;
  height: 40px;
  width: 100px;
  &__item {
    display: inline-block;
    height: 100%;
    width: 18px;
    &.-home {
      background: url('../i/icon_home.svg') 50% no-repeat;
      background-size: 18px auto;
    }
    &.-mail {
      background: url('../i/icon_mail.svg') 50% no-repeat;
      background-size: 17px auto;
    }
    &.-map {
      background: url('../i/icon_map.svg') 50% no-repeat;
      background-size: 16px auto;
    }
    a {
      display: block;
      height: 100%;
    }
  }
  &__item + &__item {
    margin-left: 16px;
  }
}

.top-nav {
  margin-left: 154px;
  height: 40px;
  &__item {
    display: inline-block;
    font-size: .6rem;
    line-height: 2rem;
    font-family: $sans;
    height: 100%;
    text-transform: uppercase;
    a {
      color: $top-nav;
      text-decoration: none;
      transition: all .4s ease;
      &:hover, &:active {
        color: $accent;
      }
    }
  }
  &__item + &__item {
    margin-left: 21px;
  }
}

.social-list {
  position: absolute;
  top: 0;
  right: 2%;
  height: 40px;
  &__item {
    display: inline-block;
    height: 100%;
    width: 18px;
    &.-fb {
      background: url('../i/icon_fb.svg') center no-repeat;
      background-size: 9px auto;
    }
    &.-vk {
      background: url('../i/icon_vk.svg') center no-repeat;
      background-size: 11px auto;
    }
    &.-tw {
      background: url('../i/icon_tw.svg') center no-repeat;
      background-size: 18px auto;
    }
    &.-lj {
      background: url('../i/icon_pencil.png') center no-repeat;
      background-size: 14px auto;
    }
    &.-yt {
      background: url('../i/icon_yt.png') center no-repeat;
      background-size: 15px auto;
    }
    &.-houzz {
      background: url('../i/icon_m.png') right no-repeat;
      background-size: 14px auto;
    }
    a {
      display: block;
      height: 100%;
    }
  }

  &__item + &__item {
    margin-left: 14px;
  }
}

header {
  position: relative;
}

.header__inner {
  @extend %center-padding;
  height: 224px;
  padding-top: 2.5%;
  position: relative;
}

header.line .header__inner {
  border-bottom: 2px solid $accent;
}

.contact-info {
  position: absolute;
  top: 55px;
  left: 1rem;
  letter-spacing: .05em;
  text-transform: uppercase;
  a {
    display: block;
  }
  a:link, a:visited {
    color: $p-color;
    text-decoration: none;
    transition: all .3s;
  }
  a:hover, a:active {
    color: $s-color;
  }
}

.contact-info__phone {
  font-size: 1rem;
  font-family: $sans;
  line-height: 1;
}

.contact-info__mail {
  font-size: .6rem;
  font-family: $sans;
  line-height: 1;
  margin-top: .8rem;
}

.logo {
  background: url('../i/logo_main.png');
  margin: 0 auto 1.6%;
  height: 118px;
  width: 138px;
}

.addition-info {
  position: absolute;
  top: 55px;
  right: 1rem;
  font-size: .6rem;
  line-height: 1;
  font-family: $sans;
  letter-spacing: .05em;
  text-align: right;
  text-transform: uppercase;
  a {
    display: block;
  }
  a:link, a:visited {
    color: $p-color;
    text-decoration: none;
    transition: all .3s;
  }
  a:hover, a:active {
    color: $s-color;
  }
}

.addition-info__club {
  margin-top: 1rem;
}

nav {
  height: 40px;
  position: relative;
}

.main-nav {
  height: 100%;
  text-align: center;
  width: 100%;
}

.main-nav__item {
  display: inline-block;
  font-size: .7rem;
  line-height: 2rem;
  font-family: $sans;
  letter-spacing: .1em;
  text-transform: uppercase;

  a {
    display: block;
    height: 100%;
    transition: all .3s;
  }
  a:link, a:visited {
    color: #000;
    text-decoration: none;
  }
  a:hover, a:active {
    color: $s-color;
  }
}

.main-nav__item + .main-nav__item {
  margin-left: 31px;
}

.search-block {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  text-align: right;
  transition: all .5s;
  width: 50px;
  &.openned {
    width: 960px;
    .search-block__input {
      width: 935px;
    }
  }
}

.search-block__input {
  background: transparent;
  border: 0;
  outline: 0;
  height: 100%;
  vertical-align: top;
  transition: all .5s;
  width: 0;
}

.search-block__submit {
  background: url('../i/icon_search.png') center no-repeat;
  background-size: 15px auto;
  border: 0;
  outline: 0;
  height: 100%;
  vertical-align: top;
  width: 15px;
}

.top-slider {
  @extend %center;
  position: relative;
  height: 600px;
}

.top-slider__container {
  img {
    max-width: 100%;
  }
  height: 100%;
  width: 100%;
}

.big-arrow {
  position: absolute;
  top: 50%;
  cursor: pointer;
  height: 58px;
  width: 30px;
  &.prev-arrow {
    background: url('../i/prev_arrow.png');
  }
  &.next-arrow {
    background: url('../i/next_arrow.png');
  }
}

.top-slider__prev {
  left: -85px;
  margin-top: -29px;
  @media (max-width: 1159px) {
    left: 20px;
  }
}

.top-slider__next {
  right: -85px;
  margin-top: -29px;
  @media (max-width: 1159px) {
    right: 20px;
  }
}

.company-desc {
  @extend %center-padding;
  @include clearfix;
  text-align: center;
}

.company-desc__title {
  font-size: 1.8rem;
  line-height: 1.28;
  font-family: $serif;
  letter-spacing: .005em;
  margin: 4.5% 0 6%;
  position: relative;
  text-transform: uppercase;
}

.company-desc__list {
  float: left;
  text-align: left;
  width: 48%;
  p {
    font-weight: bold;
    font-size: .9rem;
    line-height: 1.44;
    font-family: $sans;
    letter-spacing: .05em;
    text-transform: uppercase;
  }
  li {
    font-size: .8rem;
    line-height: 1.625;
    font-family: $sans;
    padding-left: 21px;
    text-indent: -21px;
    &:before {
      background: $s-color;
      border-radius: 50%;
      content: '';
      display: inline-block;
      height: 7px;
      margin-right: 14px;
      width: 7px;
    }
  }
}

.company-desc__list + .company-desc__list {
  margin-left: 4%;
}

.parralax-block {
  height: 320px;
  margin-top: 2.6rem;
  padding-top: 76px;
  p {
    color: #fff;
    font-size: 2.4rem;
    font-family: $serif;
    margin: 0;
    text-align: center;
    text-transform: uppercase;
  }
  a:link, a:visited {
    color: #fff;
    text-decoration: none;
  }
  a:hover, a:active {
    color: #f2f2f2;
  }
}

.collection-slider {
  @extend %center;
  margin-top: 3.5rem;
  position: relative;
}

.collection-slider__title {
  font-size: 2rem;
  line-height: 1.15;
  font-family: $serif;
  margin: 0 auto;
  max-width: 80%;
  text-align: center;
  text-transform: uppercase;
}

.collection-slider__wrap {
  @include clearfix;
  height: 500px;
  margin-top: 3.4%;
}

.collection-slider__item {
  height: 100%;
  position: relative;
  img {
    float: left;
    height: auto;
    margin-right: 10%;
    width: 40%;
    position: relative;
  }
  .item-desc {
    float: left;
    padding-top: 13%;
    position: relative;
    width: 50%;
    z-index: 2;
  }

  .item-desc__title {
    font-size: 2rem;
    font-family: $serif;
    margin: 0;
    max-width: 55%;
    text-transform: uppercase;
  }

  .item-desc__subtitle {
    font-size: .8rem;
    line-height: 1.5;
    font-family: $sans;
    margin: 25px 0 0;
    max-width: 80%;
    text-align: justify;
  }
}

.collection-slider__frame {
  position: absolute;
  bottom: 58px;
  right: 0;
  background: url('../i/bg_frame.png');
  height: 19.15rem;
  pointer-events: none;
  width: 68.6%;
  z-index: 1;
}

.company-advantage {
  @extend %center-padding;
  margin-top: 4rem;
  padding-bottom: 2.5%;
  position: relative;
  text-align: center;
  z-index: 2;
  &::before {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    border: 1px solid #f2f2f2;
    content: '';
    display: block;
    margin: 0 auto;
    width: 336px;
    z-index: -1;
  }
  img {
    margin: 4.5% 0 3.3%;
  }
  p {
    font-style: italic;
    font-size: 1.1rem;
    line-height: 1.54;
    font-family: $serif;
    margin: 0;
  }
}

.piece {
  @extend %center;
  margin-top: 3.3rem;
}

.piece__title {
  font-size: 2rem;
  font-family: $serif;
  margin: 0 0 1.1rem;
  text-align: center;
  text-transform: uppercase;
}

.piece__container {
  display: flex;
  flex-wrap: nowrap;
  align-items: stretch;
}

.piece__item {
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 33.33%;
  text-align: center;
  &:hover {
    .item__desc {
      background: $sec-color;
      border-left: 1px solid $sec-color;
      border-top: 1px solid $sec-color;
      border-right: 1px solid $sec-color;
    }
    .item__title, .item__subtitle {
      color: #fff;
    }
    img {
      opacity: 1;
    }
  }
  img {
    opacity: .5;
    transition: all .3s ease;
    width: 100%;
  }
  .item__desc {
    border: 1px solid #f2f2f2;
    border-bottom: 0;
    flex: 1 1 auto;
    padding-top: 1.4rem;
    position: relative;
    transition: all .3s ease;
    a {
      color: inherit;
      text-decoration: none;
    }
    &::after {
      position: absolute;
      bottom: 0;
      left: -1px;
      right: -1px;
      background: $sec-color;
      content: '';
      display: block;
      height: 6px;
    }
    &::before {
      position: absolute;
      bottom: -15px;
      left: 0;
      right: 0;
      background: url('../i/tri.svg');
      background-size: contain;
      content: '';
      display: block;
      height: 21px;
      margin: 0 auto;
      width: 42px;
      z-index: 3;
    }
  }
  .item__title {
    color: #333;
    font-weight: bold;
    font-size: .75rem;
    line-height: 1.33;
    font-family: $sans;
    margin: 0 auto 7.3%;
    max-width: 80%;
    position: relative;
    text-transform: uppercase;
    transition: all .3s ease;
  }
  .item__subtitle {
    color: #8d8d8d;
    font-size: .8rem;
    line-height: 1.125;
    font-family: $sans;
    margin: 0 auto 9.8%;
    max-width: 75%;
    transition: all .3s ease;
  }
  .item__image {
    height: 200px;
    overflow: hidden;
    position: relative;
    width: 100%;
    img {
      max-width: 100%;
    }
  }
}

.seo-text {
  @extend %center-padding;
  @include clearfix;
  margin: 4rem auto 0;
  h1, h2 {
    font-size: 1.2rem;
    font-family: $serif;
    font-weight: normal;
    margin-top: 0;
    text-transform: uppercase;
  }
  p {
    color: #323232;
    font-size: .9rem;
    line-height: 1.44;
    font-family: $sans;
    margin: 1.2em 0 0;
  }
  a {
    color: $t-color;
    transition: all .3s;
    &:hover, &:active {
      color: $s-color;
    }
  }
}

.seo-left {
  overflow: hidden;
  p {
    text-align: justify;
  }
}

.seo_text {
}

.showroom {
  float: right;
  margin: 0 0 0 5.5%;
  padding-bottom: 2rem;
  position: relative;
  width: 31.3%;
  img {
    max-width: 100%;
  }
}

.showroom__title {
  display: block;
  font-size: 1rem;
  line-height: 1.3;
  font-family: $serif;
  margin: 1rem 0 0;
}

.showroom__subtitle {
  display: block;
  font-weight: bold;
  font-size: .7rem;
  font-family: $sans;
  margin: 1rem 0 0;
}

.seo-text__item {
  @extend %center-padding;
  margin: 2.25rem auto 0;
  padding-bottom: .6rem;
  position: relative;
  h1, h2 {
    font-size: 1.2rem;
    font-family: $serif;
    font-weight: normal;
    margin-top: 0;
    text-transform: uppercase;
  }
  p {
    color: #323232;
    font-size: .9rem;
    line-height: 1.44;
    font-family: $sans;
    margin: 1.2em 0 0;
  }
  a {
    transition: all .3s;
  }
  a:link, a:visited {
    color: $t-color;
  }
  a:hover, a:active {
    color: $s-color;
  }
}

.carousel-block {
  @extend %center;
  display: flex;
  justify-content: space-between;
  margin: 1.75rem auto 2.6rem;
}

.carousel {
  width: 375px;
  h2 {
    font-size: 1rem;
    font-family: $serif;
    margin: 0 0 .6em;
    text-align: center;
    text-transform: uppercase;
  }

  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    display: flex;
    float: none !important;
    height: auto !important;
    flex-direction: column;
    .image-wrap {
      flex: 0 0 auto;
    }
    h3 {
      //flex: 1 1 auto;
      flex: 1 1 auto;
    }
  }
}

.carousel-container {
  @include clearfix;
  position: relative;
}

.carousel-wrap {
  @include clearfix;
}

.carousel__item {
  background: #fff;
  width: 20%;
  &.slick-current img {
    opacity: 1;
  }
  img {
    opacity: .5;
    max-width: 100%;
    transition: opacity .3s ease;
  }
  .item-desc {
    border: 1px solid #fafafa;
    padding: 1.25rem 1.2rem;
  }
  h3 {
    border: 1px solid #fafafa;
    padding: .75rem 1em;
    font-size: .6rem;
    line-height: 1.38;
    font-family: $sans;
    margin: 0;
    text-align: center;
    a {
      color: #333;
      text-decoration: none;
      transition: all .3s;
      &:hover, &:active {
        color: $s-color;
        text-decoration: none;
      }
    }
  }
}

.carousel__prev {
  position: absolute;
  top: 1.6575rem;
  left: -85px;
  @media (max-width: 1159px) {
    left: 20px;
  }
}

.carousel__next {
  position: absolute;
  top: 1.6575rem;
  right: -85px;
  @media (max-width: 1159px) {
    right: 20px;
  }
}

.white-footer {
  position: relative;
  z-index: 2;
  &::before {
    position: absolute;
    top: 9rem;
    left: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    content: '';
    display: block;
    z-index: -1;
  }
}

footer {
  @extend %center;
  border-top: 10px solid $sec-color;
  margin-top: 1.6rem;
  position: relative;
  &.zero-margin {
    margin-top: 0;
  }
}

.footer-wrap {
  padding: 0 1rem 1.5rem;
  position: relative;
  &::after {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background: #f2f2f2;
    content: '';
    display: block;
    height: 2px;
    margin: 0 auto;
    width: 1000px;
  }
}

.footer-item {
  display: inline-block;
  vertical-align: top;
}

.footer-item + .footer-item {
  margin-left: 50px;
}

.footer-item__title {
  font-weight: bold;
  font-size: .65rem;
  font-family: $sans;
  margin: 0 0 1.2em;
  padding: 1.65em 0;
  position: relative;
  text-transform: uppercase;
}

.footer-item:first-child .footer-item__title::before {
  position: absolute;
  left: -1rem;
  bottom: 0;
  background: #f2f2f2;
  content: '';
  display: block;
  height: 2px;
  width: 1000px;
}

.footer-item__list {
  font-size: .65rem;
  font-family: $sans;
  li {
    margin: .75em 0 0;
  }
  a {
    color: #000;
    text-decoration: none;
    transition: all .3s;
    &:hover, &:active {
      color: $s-color;
    }
  }
}

.footer-rights {
  color: #a1a1a1;
  font-size: .6rem;
  font-family: $sans;
  margin: 0;
  padding: 2.5em 0;
  position: relative;
  text-align: right;

}

.footer-count {
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  height: 31px;
  width: 88px;
}

.callback {
  position: fixed;
  top: 50%;
  left: 0;
  height: 354px;
  margin-top: -177px;
  width: 40px;
}

.callback-question {
  background: url('../i/callback_q.png');
  height: 162px;
  margin-top: 10px;
  width: 40px;
  a {
    display: block;
    height: 100%;
  }
}

.callback-call {
  background: url('../i/callback_c.png');
  height: 182px;
  width: 40px;
  a {
    display: block;
    height: 100%;
  }
}

/***********************
******** CLUB-BOX *****
*********************/

.club-box {
  @extend %center-padding;
  margin-top: 2rem;
  img {
    max-width: 100%;
  }
}

.breadcrumbs {
  color: #9a9a9a;
  font-size: .65rem;
  line-height: 1;
  font-family: $sans;
  margin: 0;
  padding: 0;
  a {
    color: #9a9a9a;
    text-decoration: none;
    transition: all .3s;
    &:hover, &:active {
      color: $s-color;
    }
  }
}

.page-title {
  font-size: 1.5rem;
  font-family: $serif;
  margin: 0.2em 0;
  max-width: 28rem;
  padding: 0;
  text-transform: uppercase;
}

.product-title {
  font-size: 2rem;
  font-family: $serif;
  line-height: 1.1;
  margin: 0.2em 0;
  max-width: 28rem;
  padding: 0;
  text-transform: uppercase;
}

.club-box__img {
  margin: 0 -1rem 3.6%;
}

.club-box__text {
  color: #333;
  font-size: .9rem;
  line-height: 1.44;
  font-family: $sans;
  margin: 1.6em 0;
}

.club-box__line {
  background: #e8d4e6;
  height: 1px;
