    display: block;
    padding: 0 0 0 5.4%;
    text-indent: -3.3%;
    input[type='checkbox'] {
      display: none;
    }
    input + .fake-input {
      background: url('../i/checkbox.svg');
      background-size: .9rem .9rem;
      display: inline-block;
      height: .9rem;
      margin-right: .8em;
      vertical-align: -.2em;
      transition: all .3s ease;
      width: .9rem;
    }
    input:checked + .fake-input {
      background: url('../i/checkbox_checked.svg');
      height: .9rem;
      width: .9rem;
    }
  }
  label + label {
    margin: .2em 0 0;
  }
  input[type='text'] {
    color: #565656;
    border: 1px solid #e1e1e1;
    height: 1.525rem;
    font-size: .9rem;
    line-height: 1.44;
    font-family: $sans;
    outline: none;
    padding-left: .5em;
    transition: all .3s;
    width: 31.67%;
    margin: 1% 0 0 2%;
    &:focus {
      border: 1px solid #e8d4e6;
    }
  }
}

.club-form__fieldset {
  border-top: 1px solid #e8d4e6;
  border-bottom: 1px solid #e8d4e6;
  margin: 3.3% 0;
  padding: 3% 0;
}

.club-form__protect {
  border-top: 1px solid #e8d4e6;
  margin: 1.38em 0 0;
  padding: 0 1rem;
  p {
    font-size: .9rem;
    line-height: 1.44;
    font-family: $sans;
    margin: 1.8em 0 0;
    text-transform: uppercase;
  }
  input {
    color: #565656;
    border: 1px solid #e1e1e1;
    font-size: .9rem;
    line-height: 1.44;
    font-family: $sans;
    outline: none;
    padding-left: .5em;
    transition: all .3s;
    height: 1.525rem;
    width: 15%;
    &:focus {
      border: 1px solid #e8d4e6;
    }
  }
}

.club-form__protect-code {
