.category-slider {
  border-bottom: 1px solid #e8d4e6;
  height: 33rem;
  margin: 0 -1rem;
  position: relative;
}

.category-slider__prev {
  left: -85px;
  margin-top: -85px;
  @media (max-width: 1159px) {
    left: 20px;
  }
}

.category-slider__next {
  right: -85px;
  margin-top: -85px;
  @media (max-width: 1159px) {
    right: 20px;
  }
}

.category-slider__container {
  @include clearfix;

  .slick-track {
    height: 630px;
  }
}

.category-slider__container .slick-slide {
  opacity: .4;
  margin: 0 2rem;
  position: relative;
  top: 100px;
  transition: all .7s;
  height: auto;
  width: 280px;
  .item__title, .item__desc {
    opacity: 0;
  }
}

.category-slider__container .slick-current {
  opacity: 1;
  transform: scale(1.428);
  .item__title, .item__desc {
    opacity: 1;
    transform: scale(.75);
  }
}

.category-slider {
  .category-slider__container, .slick-list, .slick-track {
    height: 100%;
  }
}

