/* CLEARFIX */
@mixin clearfix() {
	&:before,
	&:after {
		content: "";
		display: table;
	}
	&:after {
		clear: both;
	}
}

%image_replace {
	text-indent: 100%;
	white-space: nowrap;
	overflow: hidden;
}