@font-face {
    font-family: 'Proba Pro';
    src: url('../_fonts/ProbaPro-Italic.eot');
    src: url('../_fonts/ProbaPro-Italic.eot?#iefix') format('embedded-opentype'),
        url('../_fonts/ProbaPro-Italic.woff2') format('woff2'),
        url('../_fonts/ProbaPro-Italic.woff') format('woff'),
        url('../_fonts/ProbaPro-Italic.ttf') format('truetype'),
        url('../_fonts/ProbaPro-Italic.svg#ProbaPro-Italic') format('svg');
    font-weight: normal;
    font-style: italic;
}

